.message{
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 150px;
	background: salmon;
	border: 1px solid red;
	margin-bottom: 25px;
}
.message .message-close{
	position: absolute;
	top: 50%;
	transform: translate(-50%,-50%);
	right: 5px;
	cursor: pointer;
}
.message .message-bandeau{
	display: flex;
	align-items: center;
	position: relative;
	width: 100%;
	height: 25px;
	background: red;
	
}
.message .message-separator{
	width: 98%;
	border-bottom: 1px solid rgb(112, 112, 112);
	align-self: center;
}
.message .message-meta{
	padding: 5px;
	font-size: 15px;
}
.message .message-text{
	padding: 5px;
	font-size: 20px;

}